import SimpleLightbox from "simplelightbox"; // Описаний в документації
import "simplelightbox/dist/simple-lightbox.min.css"; // Додатковий імпорт стилів

const container = document.querySelector(".gallery");
const createItemsMarkup = createMarkup(images);
container.insertAdjacentHTML("beforeend", createItemsMarkup);

function createMarkup(arr) {
    return arr
    .map((item) => `
        <li class="item gallery-item">
            <a class="gallery-link" href="${item.original}">
            <img class="gallery-image"
                 src = "${item.preview}" 
                 data-source = "${item.original}"
                 alt = "${item.description}"
                 width = "360" height = "200"             
            />
            </a>
        </li>
    `).join('')
} 

// примінити бібліотеку SimpleLightbox з властивостями:
const lightbox = new SimpleLightbox('.gallery a', {
    swipeClose: false, 
    captionPosition: 'bottom', 
    captionsData: 'alt'
});

//sourceAttr - атрибут, який використовується для великих зображень
//swipeClose: проведіть пальцем вгору або вниз, щоб закрити галерею (false/true)
//captionPosition: - положення підпису. 
//captionsData:  - назва картинки під фото